<template>
    <div id="app" class="d-flex" ref="root">
        <side-bar :items="sidebarItems" />
        <div class="d-flex flex-column w-100">
            <router-view />
        </div>
    </div>
</template>

<script>
import API from "./api/ApiUtils.js";
import SideBar from "@/components/SideBar.vue";

export default {
    components: {
        SideBar,
    },
    data() {
        return {
            API: new API(this),
            sidebarItems: [],
        };
    },
    methods: {
        fetchSidebarItems() {
            this.API.getSidebarItems().then((data) => {
                this.sidebarItems = data;
            });
        },
    },
    mounted() {
        this.fetchSidebarItems();
    },
};
</script>

<style lang="scss">
@import "./styles.scss";
</style>
